.navbar {
	position: fixed;
	/*margin-top: 9rem;*/
	width: 100%;
	z-index: 500;
	color: #ffffff;
}

.navbar__nav {
	display: flex;
	justify-content: space-between;
	max-width: 90%;
	margin: 0 auto;
}

.navbar__bg--initial {
	background: none;
}

.navbar__bg--scroll {
	background: #418e42;
}

.navbar__ul {
	padding: 0.5rem;
}

.navbar__list-items {
	display: inline-flex;
	padding: 0.5rem 1rem;
	font-weight: bold;
}

.navbar__list-items a {
	text-decoration: none;
	color: #ffffff;
}

.navbar__list-items a:hover,
.navbar__list-items a:active {
	font-weight: 800;
}

@media screen and (max-width: 1065px) {
	.navbar__ul--left,
	.navbar__ul--right {
		display: none;
	}

	.navbar {
		display: inline-flex;
	}
}

@media screen and (min-width: 1066px) {
	.toggle-button {
		display: none;
	}
}
