/* Grid and Flex layouts */
.grid__container,
.flex__container {
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
}

.grid__row {
	width: 100%;
	display: grid;
	grid-gap: 1rem;
}

.flex__row {
	width: 100%;
	display: -webkit-flex;
	display: flex;
}

.flex-basis--25 {
	-webkit-flex-basis: 25%;
	        flex-basis: 25%;
}

.grid__column--one-sixth {
	width: 16.66%;
}
.grid__column--one-fourth {
	width: 25%;
}
.grid__column--one-third {
	grid-template-columns: 1fr 1fr 1fr;
}
.grid__column--one-half {
	grid-template-columns: 1fr 1fr;
}
.grid__column--two-thirds {
	width: 66.66%;
}
.grid__column--three-fourths {
	width: 75%;
}
.grid__column--five-sixths {
	width: 83.33%;
}
.grid__column--twelve {
	width: 100%;
}

.outline,
.outline * {
	outline: 1px solid #f6a1a1;
}

/*-- some extra column content styling --*/
/*[class*='grid__column--'] > p {
    padding: 0;
    margin: 0;
}*/

/* Responsive Grid */
@media screen and (max-width: 800px) {
	.grid__column--one-sixth {
		width: 33.33%;
	}
	.grid__column--one-fourth {
		width: 50%;
	}
	.grid__column--one-third {
		grid-template-columns: 1fr 1fr;
	}
	.grid__column--one-half {
		grid-template-columns: 1fr 1fr;
	}
	.grid__column--two-thirds {
		width: 100%;
	}
	.grid__column--three-fourths {
		width: 100%;
	}
	.grid__column--five-sixths {
		width: 100%;
	}
	.grid__column--twelve {
		width: 100%;
	}

	.grid__row .grid__column--one-third:first-of-type {
		width: 100%;
	}

	.grid__row .grid__column--five-sixths ~ .grid__column--one-sixth {
		width: 100%;
	}
}

@media screen and (max-width: 650px) {
	[class*='grid__column--'] {
		grid-template-columns: 1fr;
	}
}


